.chess {
    width: 50px;
    height: 50px;
    border: 2px solid rosybrown;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    box-sizing: border-box;
}

.chess .chess-item {
    width: 40px;
    height: 40px;
    border-radius: 50%;
}

.chess .red {
    background: radial-gradient(#fff, red);
}

.chess .black {
    background: radial-gradient(#fff, #000);
}